import { Injectable, EventEmitter } from '@angular/core';

/**
 * @hidden
 */
@Injectable()
export class DetailsService {
    public changes: EventEmitter<any> = new EventEmitter<any>();
    private rowState: number[] = [];

    public toggleRow(index: number, dataItem: any): void {
        const rowIndex: number = this.rowState.indexOf(index);
        const expand: boolean = rowIndex === -1;

        this.rowState = expand ?
            [...this.rowState, index] :
            [...this.rowState.slice(0, rowIndex), ...this.rowState.slice(rowIndex + 1)];

        this.changes.emit({ dataItem: dataItem, expand: expand, index: index });
    }

    public isExpanded(index: number): boolean {
        return this.rowState.indexOf(index) >= 0;
    }
}



